import Vue from "vue";
import Vuex from "vuex";

import * as constants from "./constants";

Vue.use(Vuex);

export default new Vuex.Store({
  strict: process.env.NODE_ENV !== "production",
  state: {
    apiKey: null,
    diets: null,
    cuisines: null,
    intolerances: null,
    mealTypes: null,
    ingredients: null,
    sort: "default",
    sortDirection: "asc",
    searching: false,
    lastSearchUrl: null,
    lastResults: null,
    lastOffset: 0,
    lastTotalResults: 0
  },
  getters: {
    dietOptions: () => {
      return constants.dietsList;
    },
    cuisineOptions: () => {
      return constants.cuisineList;
    },
    intoleranceOptions: () => {
      return constants.intoleranceList;
    },
    mealOptions: () => {
      return constants.mealTypeList;
    },
    sortOptions: () => {
      return constants.sortTypeList;
    }
  },
  mutations: {
    setApiKey(state, payload) {
      state.apiKey = payload.apiKey;
    },
    setDiets(state, payload) {
      state.diets = payload.selectedOptions;
    },
    setCuisines(state, payload) {
      state.cuisines = payload.selectedOptions;
    },
    setIntolerances(state, payload) {
      state.intolerances = payload.selectedOptions;
    },
    setMeals(state, payload) {
      state.mealTypes = payload.selectedOptions;
    },
    setIngredients(state, payload) {
      state.ingredients = payload.selectedOptions;
    },
    setSorting(state, payload) {
      state.sort = payload.sort === "default" ? null : payload.sort;
      state.sortDirection =
        payload.sortDirection === "asc" || payload.sortDirection === "desc"
          ? payload.sortDirection
          : "asc";
    },
    toggleSearching(state) {
      state.searching = !state.searching;
    },
    setLastSearchUrl(state, payload) {
      state.lastSearchUrl = payload.url;
    },
    setLastSearchResults(state, payload) {
      state.lastTotalResults = payload.totalResults;
      state.lastResults = payload.results;
      state.lastOffset = payload.offset;
    }
  },
  actions: {
    runQuery: async ({ commit, state }) => {
      commit("toggleSearching");

      // build the full search url
      let fullUrl = `${constants.apiSearchUrl}?apiKey=${state.apiKey}&addRecipeInformation=true`;
      if (state.diets && state.diets.length > 0) {
        fullUrl += `&diet=${state.diets.join(",")}`;
      }
      if (state.cuisines && state.cuisines.length > 0) {
        fullUrl += `&cuisine=${state.cuisines.join(",")}`;
      }
      if (state.intolerances && state.intolerances.length > 0) {
        fullUrl += `&intolerances=${state.intolerances.join(",")}`;
      }
      if (state.mealTypes && state.mealTypes.length > 0) {
        fullUrl += `&type=${state.mealTypes.join(",")}`;
      }
      if (state.ingredients && state.ingredients.length > 0) {
        fullUrl += `&includeIngredients=${state.ingredients.join(",")}`;
      }
      fullUrl += `&sort=${state.sort}&sortDirection=${state.sortDirection}`;

      // update the last search url so we can have it around if the form changes
      commit({
        type: "setLastSearchUrl",
        url: encodeURI(fullUrl)
      });

      // fetch some data!
      const response = await fetch(state.lastSearchUrl, {
        method: "GET",
        mode: "cors",
        cache: "no-cache"
      });

      if (response.ok) {
        const responseData = await response.json(); // parses JSON response into native JavaScript objects

        commit({
          type: "setLastSearchResults",
          offset: responseData.offset,
          results: responseData.results,
          totalResults: responseData.totalResults
        });

      } else {
        // do something if it wasn't ok
        commit({
          type: "setLastSearchResults",
          offset: 0,
          results: null,
          totalResults: 0
        });
      }

      commit("toggleSearching");
    }
  },
  modules: {}
});
