<template>
  <v-flex mt-3 xs12 v-if="lastResults">
    <v-data-table
      v-model="selectedSearchResults"
      :headers="headers"
      :items="lastResults"
      item-key="id"
      show-select
      class="elevation-1"
      disable-sort
      hide-default-footer
    >
      <template v-slot:item.image="{ item }">
        <v-img
          :src="
            `https://spoonacular.com/recipeImages/${item.id}-90x90.${item.imageType}`
          "
          max-width="90"
        ></v-img>
      </template>
    </v-data-table>
  </v-flex>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "SearchResults",

  computed: {
    ...mapState([
      "searching",
      "lastResults",
      "lastTotalResults",
      "lastOffset",
      "lastSearchUrl"
    ])
  },

  data: () => ({
    selectedSearchResults: [],
    headers: [
      {
        align: "left",
        value: "image"
      },
      {
        text: "ID",
        align: "left",
        sortable: false,
        value: "id",
        class: ["font-weight-black", "subtitle-2"]
      },
      {
        text: "TITLE",
        align: "left",
        sortable: false,
        value: "title",
        class: ["font-weight-black", "subtitle-2"]
      }
    ]
  })
};
</script>
